.opcoes {
    display: flex;
    align-items: center;
    width:auto;
    height:20px;
    align-items: center;
    justify-items: center;
    text-align: center;
}

.position-opcoes {
    position: relative;
    top:140px;
}

.position-opcoes h1{
    position: relative;
    top:-100px;
}

.bloco-step-completo {
    width: auto;
    min-width: 243px;
    height: 210px !important;
    color:#fff !important;
    background-color: #37BBAA !important;
    user-select: none;
}



.bloco-step-completo h1{
   position: relative;
   left:20px;
   top:-18px;
}



.bloco-parcial {
    width: auto;
    min-width: 243px;
    height: 210px;
    color:#fff !important;
    background-color: #FFCF64 !important;
    user-select: none;
}


.btn-sucess {
    width: 167px !important;
    height: 109px !important;
    border-radius: 100px !important;
}

.btn-continue {
    float: right !important;
    position: relative;
    top:55px;
    left:-10px;
}
 

.bloco-parcial h1{
   position: relative;
   left:48px;
