    color:#fff !important;
    background-color: #FE5B3A !important;
     user-select: none;
}



.bloco-ausente h1{
   position: relative;
   left:38px;
   top:-18px;
}

.btn-back-checklist {
    position: relative;
    left:10px;
    top:8%;
    float: left;
}

.header-card-paciente {
    color: #fff !important;
    background-color: #37BBAA;
}

.bloco-info-paciente-inducao {
    width: auto;
    height:auto;
    position: relative;
    text-align: center;
    display:flex;
    font-size: 22px;
}

.bloco-info-paciente-inducao div {
    padding-left:15px;
    padding-right:15px;
}

.header-nome-paciente {
    font-size: 32px;
}

.header-card-checklist {
    color: #fff;
    background-color: #4D4F53 !important;
}

.header-checklist {
    font-size: 30px;
}

tr > td {
    font-size: large;
}

tr > th{
    font-size: large;
}

.checks{   
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 80%;
    width: 30px;
    height: 30px;
    border: 2px solid #999;
    transition: 0.2s all linear;
    margin-right: 5px;
    position: relative;
    top: 4px;
}

.checks:checked{
    background: #6983F5;
}

.input-time{

    width: 140px;
    height: 50px;   
    font-size: 25px;
    color:#fff;
    border:none;
    text-align: center;
    padding-left:30px;
    background: #6983F5;
    border-radius: 30px;
}

.ds-resposta {
    position: relative;
    left:65px;
    top:-15px;
    font-size: 22px;
    margin-top: -35px;
}



